.settings_wrapper {
    margin: 25px 25px 50px;
    height: auto;
    max-width: 100%;
    border-radius: 10px;
}

.upload_Photo_wrapper {
    width: 100%;
    padding: 10px;
    border: 1px solid #8E8888;
    border-radius: 5px;
    backdrop-filter: blur(25px) brightness(85%);
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.upload_Photo_wrapper span {
    display: inline-block;
    width: 35%;
    color: #E8E0CF;
}

.upload_Photo_wrapper input[type="file"] {
    width: 65%;
    background: #443F49;
    color: whitesmoke;
}

.settings_form {
    border: 1px solid #8E8888;
    border-radius: 5px;
    margin-top: 25px;
    position: relative;
    backdrop-filter: blur(25px) brightness(85%);
}

.settings_form input,
.settings_form textarea {
    font-size: 16px;
}

.settings_box_wrapper {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px;
}

.settings_box_wrapper span {
    display: inline-block;
    width: 35%;
    color: #E8E0CF;
}

.settings_box_wrapper .input_info_wr {
    display: inline-block;
    width: 65%;
}

.settings_box_wrapper input {
    width: 100%;
    border-radius: 5px;
    background: #9E9898;
    padding: 5px;
    color: #443F49;
    text-indent: 5px;
}

.settings_box_wrapper textarea {
    background: #9E9898;
    width: 100%;
    resize: vertical;
    min-height: 60px;
    max-height: 140px;
    border-radius: 5px;
    color: #443F49;
    padding: 5px;
    text-indent: 5px;
}

.settings_box_wrapper input[type=checkbox] {
    width: 16px;
    height: 16px;
}

.saveBtn_action_wrapper {
    position: relative;
    bottom: -40px;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.saveBtn_action_wrapper button {
    width: 100px;
    height: 30px;
    border-radius: 5px;
}

.saveBtn_action_wrapper button:active {
    transform: scale(0.99);
    background: #E8E0CF;
}

.contacts_header {
    text-align: center;
    display: block;
    width: 100%;
    margin: 0 0 10px;
}

.updating_success {
    color: green;
    font-size: 18px;
    margin-left: 10px;
}


/* media styles for settings page */


@media screen and (max-width: 991px) {
    .settings_wrapper {
        margin: 25px 15px 50px;
    }
}


@media screen and (max-width: 639px) {
    .settings_form,
    .settings_form input,
    .settings_form textarea,
    .upload_Photo_wrapper span {
        font-size: 14px;
    }
}

/* media styles for settings page end*/